.certificates-section {
    background-color: #d1d3d4;
    height: 100%;
    width: 100%;
}
.title{
    padding-top: 5rem;
    color: black;
    font-weight: bold;
    text-align: center;
    font-size: 20pt;
}
.section{
    margin: 0 0 0 8.6cm;
    width: 50%;
    height: 100%;
    display: block;
}
.cert-pic{
    width: 90%;
    height: 10cm;
    margin-left: 2rem;
    margin-bottom: 1.5rem;
}
.certificate-card{
    border-radius: 15px;
    margin-top: 2.5rem;
    background-color: white;
}
.cert-name{
    font-weight: bold;
    text-align: center;
    background-color: var(--s-color);
    padding-top: 0.6rem;
    height: 4rem;
}
.more{
    height: 4rem;
}
.more-button{
    background-color: #3a6cf4;
    color: #fff;
    margin-left: 88vh;
    width: 3cm;
    height: 2rem;
    border-radius: 15px;
    border: #3a6cf4 solid 2px;
    margin-top: 1rem;
    font-size: 1em;
    font-weight: bolder;
    cursor: pointer;
}
.more-button:hover{
    opacity: 0.9;
    transition: 0.2s;
}
@media (max-width: 768px) {
    .title {
        padding-top: 2rem;
        font-size: 18pt;
    }

    .section {
        margin: 0;
        width: 90%;
    }

    .cert-pic {
        width: 100%;
        height: auto;
        margin: 1rem auto;
    }

    .certificate-card {
        margin-top: 1rem;
    }

    .cert-name {
        padding-top: 0.5rem;
        height: auto;
    }

    .more-button {
        margin-left: calc(50% - 1.5cm);
        width: 6cm;
        height: 2rem;
        font-size: 10pt;
    }
}

@media (max-width: 576px) {
    .title {
        font-size: 16pt;
    }

    .cert-pic {
        width: 100%;
        height: auto;
    }

    .more-button {
        width: 5cm;
        height: 2rem;
        font-size: 9pt;
    }
}