:root{
    --f-color : #1e0bc8;
    --s-color : #05aede;
    --t-color : #010004;
    --bg1-color : #a4a6a7;
    --bg2-color : #ffffff;
    --primary-color: #2c3e50;      /* Dark blue */
    --secondary-color: #3498db;    /* Blue */
    --accent-color: #e74c3c;       /* Red */
    --background-color: #ecf0f1;   /* Light grey */
}
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.header-container {
    color: #3a6cf4;
    width: 100%;
    height: 3.5rem;
    top: 0;
    background-color: #f0f0f0;
    padding-left: 1.3rem;
    display: inline-flex;
    position: fixed;
    z-index: 999;
}
.header-container ul{
    list-style-type: none;
    display: flex;
    margin-left: 18em;
}
.header-container ul li{
    margin: 0.6rem 1.5rem 0 0;
    padding: 1px 8px 5px 5px;
}
.header-container ul li a{
    text-decoration: none;
    color: #3a6cf4;
    font-weight: bold;
    font-size: 1.2rem;
}
.header-container ul li a:hover{
    cursor: pointer;
    color: var(--f-color);
    transition: 0.4s;
    position: relative;
}
.header-container h2{
    margin-left: 6rem;
    font-size: 28px;
    margin-top: 0.45rem;
    color: var(--f-color);
}
.header-container ul li.active{
    border: 2px solid blue;
    border-radius: 10px;
    color: var(--f-color);
    transition: 0s;
}
.app-logo{
    background-color: white;
    width: 30px;
    height: 30px;
    border-radius: 30px;
    margin-top: 0.5rem;
}
@media(max-width: 1200px){
    .header-container h2{
        margin-left: 1rem;
    }
    .header-container ul{
        margin-left: 6em;
    }
}
@media(max-width: 911px){
    .header-container ul{
        margin-left: 6em;
    }
}
@media (max-width: 768px) {
    .header-container {
        flex-direction: column;
        align-items: center;
        height: auto;
        padding: 1rem;
        position: relative;
    }

    .header-container ul {
        flex-direction: column;
        margin-left: 0;
        margin-top: 1rem;
    }

    .header-container ul li {
        margin: 0.5rem 0;
        text-align: center;
        border-bottom: 0.2rem solid #3a6cf4;
    }

    .header-container h1, .header-container h2 {
        margin-left: 0;
        text-align: center;
        font-size: 1.5rem;
    }
}

@media (max-width: 576px) {
    .header-container {
        padding: 0.5rem;
        text-align: center;
    }
    .header-container ul li{
        border-bottom: #3a6cf4 1px solid;
    }
    .header-container ul li a {
        font-size: 16px;

    }

    .header-container h2 {
        font-size: 24px;
    }
}