/* Projects.css */
.projects-section {
    margin-top: 3rem;
    padding: 2rem;
    background-color: #d1d3d4;
    color: var(--t-color);
    text-align: center;
    height: max-content;
}

.projects-section h2 {
    font-size: 2.5rem;
    margin-bottom: 2rem;
}

.projects-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
}

.project-card {
    background-color: var(--bg2-color);
    border: 1px solid var(--t-color);
    border-radius: 8px;
    padding: 1rem;
    width: 300px;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.project-card img {
    width: 100%;
    height: auto;
    border-radius: 8px;
}

.project-card h3 {
    font-size: 1.5rem;
    margin: 1rem 0;
}

.project-card p {
    font-size: 1rem;
    color: var(--secondary-color);
}

.project-link {
    display: inline-block;
    margin-top: 1rem;
    text-decoration: none;
    color: var(--f-color);
    font-weight: bold;
    transition: color 0.3s ease;
}

.project-link:hover {
    color: var(--accent-color);
}
