body{
    background-color: #d1d3d4;
}
.contact-form {
    background-color: white;
    margin: 15rem 0 20rem 500px;
    width: 300px;
    padding: 2rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.contact-form label {
    display: block;
    margin-bottom: 0.5rem;
}

.contact-form input,
.contact-form textarea {
    width: 100%;
    padding: 0.5rem;
    margin-bottom: 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.contact-form button {
    padding: 0.5rem 1rem;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.contact-form button:hover {
    background-color: #0056b3;
}
@media (max-width: 768px) {
    .contact-form {
        margin: 2rem auto;
        width: 90%;
        padding: 1rem;
    }

    .contact-form button {
        width: 100%;
        padding: 0.75rem;
    }
}

@media (max-width: 576px) {
    .contact-form {
        margin: 1rem auto;
        width: 100%;
        padding: 1rem;
    }

    .contact-form button {
        width: 100%;
        padding: 1rem;
    }
}