.skills-section {
    padding: 2rem;
    background-color: #fff;
    color: var(--t-color);
    text-align: center;
}

.skills-section h2 {
    font-size: 2rem;
    margin-bottom: 1.5rem;
}

.skills-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
}

.skill-card {
    background-color: var(--bg2-color);
    border: 1px solid #fff;
    border-radius: 8px;
    padding: 1rem;
    width: 150px;
    transition: transform 0.3s ease;
    box-shadow: 0 5px 25px rgba(0, 0, 0, 70%);

}

.skill-card:hover {
    transform: scale(1.05);
}

.skill-card h3 {
    font-size: 1.2rem;
    margin-bottom: 0.5rem;
}

.skill-card p {
    font-size: 1rem;
    color: var(--s-color);
}
.skill-card img{
    width: 4rem;
    height: 4rem;
}
@media (max-width: 768px) {
    .skills-section {
        padding: 1.5rem;
    }

    .skills-section h2 {
        font-size: 1.8rem;
        margin-bottom: 1rem;
    }

    .skill-card {
        width: 45%;
        padding: 0.8rem;
    }

    .skill-card h3 {
        font-size: 1rem;
    }

    .skill-card p {
        font-size: 0.9rem;
    }

    .skill-card img {
        width: 3.5rem;
        height: 3.5rem;
    }
}

@media (max-width: 576px) {
    .skills-section {
        padding: 1rem;
    }

    .skills-section h2 {
        font-size: 1.5rem;
        margin-bottom: 0.8rem;
    }

    .skill-card {
        width: 20%;
        padding: 0.5rem;
        margin-bottom: 1rem;
    }

    .skill-card h3 {
        font-size: 0.6rem;
    }

    .skill-card p {
        font-size: 0.6rem;
    }

    .skill-card img {
        width: 2.5rem;
        height: 2.5rem;
    }
}