.home-container{
    width: 100%;
    height: 100%;
}
.about{
    width: 100%;
    min-height: 100vh;
    display: flex;
    background-image: url("../pictures/background3.png");
    background-size: cover;
    background-position: bottom left;
    background-repeat: no-repeat;
    background-attachment: fixed;

}
.about-text{
    width: 50%;
    height: 100%;
    left:0;
}
.about-text h2{
    color: #f3ff3e;
    margin-top: 6rem;
    margin-left: 4rem;
    text-align: center;
    border-bottom: #f3ff3e solid 1px;
}
.about-text p{
    text-align: -webkit-center;
    margin: 1rem 0 0 3rem;
    border-radius: 10px;
    color: #fff;
    font-size: 1.4rem;
    font-weight:  400;
}
.about-img{
    height: 100%;
    width: 50%;
    right: 0;
}
.about-img-top{
    border-radius: 20px;
    width: 12cm;
    height: 12cm;
    margin-top: 9rem;
    margin-left: 5rem;
}
.thanking-message{
    width: 100%;
    height: 4cm;
    background-color: white;
    text-align: center;
    padding: 1rem 4cm 0 4cm;
}
.thanking-message p a{
    text-decoration: none;
    color: var(--s-color);
}
@media(max-width: 1200px){
    .about-text{
        width:100vh;
        margin-left: 2rem;
    }
    .about-img{
        width: 30rem;
        margin-left: 2rem;
    }
    .about-img-top{
        margin-left: 0;
        margin-right: 0;
    }
}
@media(max-width: 1000px){
    .about-img{
        width: 70vh;
        order: 1;
    }
    .about-text{
        order: -1;
    }
}
@media (max-width: 768px) {
    .about {
        flex-direction: column;
        align-items: center;
        height: auto;
        padding: 1rem;
    }

    .about-text, .about-img {
        width: 100%;
        text-align: center;
    }
    .about-img{
        order: -1;
    }
    .about-text{
        order: 1;
    }

    .about-text h2 {
        margin: 2rem 0 2rem 0;
        padding-bottom: 1rem;
    }

    .about-text p {
        margin: 1rem auto;
        width: 90%;
    }

    .about-img-top {
        width: 8cm;
        height: 8cm;
        margin-top: 2rem;
        margin-left: 0;
    }

    .thanking-message {
        padding: 1rem 2cm;
    }
}

@media (max-width: 576px) {
    .about-container {
        display: flex;
        flex-direction: column; /* Ensure column layout for smaller screens */
    }

    .about-img-top {
        order: -1;
        width: 6cm;
        height: 6cm;
    }

    .about-text {
        order: 1;
    }

    .about-text h2 {
        font-size: 1.5rem;
    }

    .about-text p {
        font-size: 16px;
        width: 95%;
    }

    .thanking-message {
        padding: 1rem 1cm;
        font-size: 14px;
    }
}